"use client";
import { useState } from "react";

const tickers = ["AAPL", "MSFT", "AMZN", "GOOGL"];

export default function sidebar({ setSelectedTicker }) {
    return (
        <aside className="w-42 h-auto bg-darkgray p-4 pt-20 flex flex-col">
            {tickers.map((ticker) => (
                <button
                    key={ticker}
                    onClick={() => setSelectedTicker(ticker)}
                    className="text-gray-300 hover:bg-gray-700 p-2 rounded transition"
                >
                    {ticker}
                </button>
            ))}
        </aside>
    );
}
