"use client";
import { useState } from "react";

export default function Topbar({ setSelectedTicker }) {
    const [search, setSearch] = useState("");

    return (
        <div className="flex items-center justify-between bg-darkgray p-4 shadow-md">
            {/* Search Bar */}
            <input
                type="text"
                placeholder="Search companies"
                className="w-1/2 bg-darkgray border border-gray-200 rounded rounded-xl text-white p-2 rounded hover:bg-gray-300 hover:text-gray-800"
                value={search}
                onChange={(e) => setSearch(e.target.value)}
                onKeyDown={(e) => e.key === "Enter" && setSelectedTicker(search.toUpperCase())}
            />
        </div>
    );
}
